import React from 'react';
import { List, Image } from 'semantic-ui-react';
import { useHistory } from 'react-router-dom';

const image = `data:image/webp;base64,`;

let selectedItem = '';
const Sidebar = (props) => {
	let history = useHistory();

	const Logout = () => {
		console.log('logout');
		localStorage.clear();
		props.checkAuth();
		history.push('/');
	};

	const renderCategoryList = props.categories.map((category) => {
		return (
			<List.Item
				key={category.ref_id}
				active={category.ref_id === selectedItem}
				onClick={() => {
					selectedItem = category.ref_id;
					props.onCategorySelect(category.ref_id);
				}}
			>
				<Image avatar src={image} />
				<List.Content>
					<List.Header>{category.name}</List.Header>
				</List.Content>
			</List.Item>
		);
	});
	return (
		<div>
			<nav>
				<div>
					<h2>Sahl</h2>
				</div>

				<List selection verticalAlign="middle">
					{renderCategoryList}
				</List>
			</nav>
			<List selection verticalAlign="middle">
				<List.Item key="logout" onClick={() => Logout()}>
					<Image avatar src={image} />
					<List.Content>
						<List.Header>Logout</List.Header>
					</List.Content>
				</List.Item>
			</List>
		</div>
	);
};

export default Sidebar;
